@font-face {
  font-family: $generalFontFamily, sans-serif;
  src: url("https://fonts.googleapis.com/css?family=Roboto");
}

body {
  font-family: $generalFontFamily;
  overflow-x: hidden;
  width: 100%;
  padding: 25px;
  line-height: 1.5;
  color: $secondaryColor;
}

h2,
h3 {
  position: relative;
  font-family: $generalFontFamily;
  text-transform: uppercase;
  padding-bottom: 14px;
  margin-bottom: 30px;
  // font-size: 22px;
  font-size: 1.375rem;
}
h2:after,
h3:after {
  content: "";
  position: absolute;
  @include fullbox(72px);
  left: -webkit-calc(50% - 36px);
  // left: calc(50% - 36px);
  bottom: 0px;
  @include heighBackgound(3px, $primaryBackroundColor);
  // height: 3px;
  // background: $primaryBackroundColor;
}

.title-main,
h1 {
  line-height: 1.2;
  // font-size: 30px;
  font-size: 1.875rem;
}

.title-main span:last-child,
h1 span:last-child {
  // font-size: 23px;
  font-size: 1.4375rem;
}

.title-sub {
  // font-size: 48px;
  font-size: 3rem;
  position: relative;
}

h1 {
  // font-size: 22px;
  font-size: 1.375rem;
  display: inline-block;
}

.nav-main a:hover {
  color: $primaryBackroundColor;
  font-weight: bold;
}

@media (min-width: 500px) {
  .caption-blurb {
    // font-size: 18px;
    font-size: 1.125rem;
  }
}
